@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR);
/*reset*/
html,
body {
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
  background-color: #fff;
}
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
button,
select {
  margin: 0;
  padding: 0;
}
body,
input,
textarea,
select,
button,
table {
  font-family: "Malgun Gothic", Dotum, AppleGothic, sans-serif;
  font-size: 12px;
}
img,
fieldset {
  border: 0;
}
img {
  vertical-align: top;
}
table {
  border-collapse: collapse;
}
ul,
ol {
  list-style: none;
}
em,
address {
  font-style: normal;
}
a {
  text-decoration: none;
}

#about {
  padding: 70px 50px 90px 50px;
  border-bottom: 1px solid #ccc;
}

#about .title {
  font-size: 4em;
  text-decoration: underline;
  -webkit-text-decoration-color: #ff0;
          text-decoration-color: #ff0;
}
#about .subTitle {
  margin-top: 20px;
  font-size: 2.5em;
  text-align: left;
}
#about .content {
  margin-top: 20px;
  font-size: 1.5em;
  text-align: justify;
  line-height: 1.8;
}

.skills {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}
.skills ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.skills ul .react {
  background: url(data:image/png;base64,);
}
.skills ul li {
  margin: 0 10px;
}
@media only screen and (min-width: 320px) and (max-width: 667px) {
  #about {
    padding: 70px 20px;
  }
}

#contact {
  padding-top: 70px;
  height: 350px;
}
#contact .title {
  font-size: 4em;
  text-decoration: underline;
  -webkit-text-decoration-color: #0ff;
          text-decoration-color: #0ff;
}
#contact .content {
  font-size: 1.5em;
  margin-top: 30px;
  color: #333;
  font-weight: bold;
}
#contact .content .email {
  margin-bottom: 10px;
}
#contact .content a {
  color: #333;
}
#contact .content a:hover {
  text-decoration: underline;
}

#nav {
  width: 100%;
  height: 70px;
  z-index: 9999;
}
#nav > div {
  position: relative;
  height: 100%;
}
#nav .tab {
  position: absolute;
  font-size: 4em;
  left: 50px;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
  cursor: pointer;
  display: none;
}
#nav ul {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#nav li:hover {
  border-bottom: 2px solid #333;
}

#nav a {
  display: inline-block;
  font-size: 1.8em;
  color: #fff;
  line-height: 2;
  margin: 10px 0;
  padding: 0 30px;
  font-weight: bold;
}
#nav a:focus {
  outline: none;
}
#nav:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.nonScroll {
  position: absolute;
}
.scroll {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
}

@media only screen and (min-width: 320px) and (max-width: 667px) {
  #nav {
    background-color: rgba(0, 0, 0, 0.8);
  }
  #nav .tab {
    display: inline;
    z-index: 9999;
  }
  #nav ul {
    display: block;
    position: fixed;
    width: 100%;
    top: -100%;
    -webkit-transition: 0.6s ease;
    transition: 0.6s ease;
  }
  #nav ul.show {
    top: 10%;
  }
  #nav li {
    background-color: rgba(0, 0, 0);
    z-index: 9998;
  }
  #nav li:hover {
    border: none;
  }
}

#project {
  padding-top: 60px;
  border-bottom: 1px solid #ccc;
}
#project .title {
  font-size: 4em;
  text-decoration: underline;
  -webkit-text-decoration-color: #f0f;
          text-decoration-color: #f0f;
}
#project .list {
  display: flex;
  justify-content: space-around;
  margin: 60px 40px;
}
#project .card {
  position: relative;
  width: 300px;
  height: 450px;
  margin: 20px 0;
  background-color: #000;
  overflow: hidden;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
}
#project .card .projectImg {
  position: relative;
  overflow: hidden;
}

#project .card .projectImg::before {
  content: "";
  position: absolute;
  bottom: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(bottom, #000 50%, transparent);
  background: linear-gradient(0deg, #000 50%, transparent);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: 1;
}
#project .card:hover .projectImg::before {
  bottom: 0;
}
#project .card .projectImg img {
  width: 100%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
#project .card:hover .projectImg img {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}
#project .card .details {
  position: absolute;
  padding: 20px;
  width: 100%;
  height: 60%;
  bottom: -100%;
  left: 0;
  box-sizing: border-box;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: 2;
}
#project .card:hover .details {
  bottom: 0;
}
#project .card .details h3 {
  color: #fff;
  margin: 0;
  padding: 0;
  font-size: 2em;
}
#project .card .details .tag {
  position: relative;
  margin-top: 5px;
}
#project .card .details .tag span {
  padding: 2px 5px;
  margin-right: 2px;
  font-weight: bold;
  display: inline-block;
  border-radius: 4px;
}
#project .card .details .tag span.JS {
  color: #000;
  background-color: #ff0;
}
#project .card .details .tag span.node {
  color: #669f64;
  background-color: #fff;
}
#project .card .details .info {
  color: #fff;
  font-size: 1.2em;
}
#project .card .details .info p {
  margin: 15px 0 10px;
}

@media only screen and (min-width: 320px) and (max-width: 1340px) {
  #project .list {
    display: block;
    margin: 0 auto;
  }
  #project .card {
    margin: 30px auto;
  }
}
@media only screen and (min-width: 320px) and (max-width: 667px) {
  #project .card .projectImg::before {
    bottom: 0;
  }
  #project .card .details {
    bottom: 0;
  }
}

#slider {
  position: relative;
}
#slider .sliderHeader {
  position: absolute;
  width: 100%;
  text-align: center;
  padding-top: 30vh;
  z-index: 999;
}
#slider .sliderTitle {
  font-size: 6vw;
  color: #fff;
}

#slider .sliderText {
  position: absolute;
  width: 100%;
  bottom: 10vh;
  text-align: center;
  z-index: 9990;
}
#slider .sliderText a {
  color: #fff;
  font-size: 5vw;
  font-weight: bold;
  margin-left: 20vw;
  -webkit-transition: background-color 0.6s ease;
  transition: background-color 0.6s ease;
  background-color: rgba(0, 0, 0, 0.8);
}
#slider .sliderText a:hover {
  background-color: #fff;
  color: #333;
}
#slider .prev::before {
  content: "\276E";
}
#slider .next::before {
  content: "\276F";
}
#slider .prev,
#slider .next {
  position: absolute;
  top: 50%;
  width: auto;
  padding: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 2vw;
  -webkit-transition: 0.6s ease;
  transition: 0.6s ease;
  background: none;
  border: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  line-height: 1;
  z-index: 9990;
}
#slider .next {
  right: 0;
  border-radius: 3px 0 0 3px;
}
#slider .prev:focus,
#slider .next:focus {
  outline: none;
}
#slider .prev:hover,
#slider .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

#slider img {
  width: 100%;
  height: 100vh;
}

@media only screen and (min-width: 320px) and (max-width: 667px) {
  #slider .sliderHeader {
    padding-top: 20vh;
  }
  #slider img {
    height: 50vh;
  }
  #slider .prev,
  #slider .next {
    font-size: 10vw;
  }
  #slider .sliderTitle {
    font-size: 7vw;
    color: #fff;
  }
}

#wrap {
  max-width: 1600px;
  margin: 0 auto;
  text-align: center;
  font-family: "Noto Sans KR", sans-serif;
}

